import React from "react"
import { RPTooltip } from "@rp/react/web/common/base-components"
import { downloadFile } from "@rp/react/web/common/files"
import { IconButton } from "@rp/react/web/mui/core"
import { Download } from "@rp/react/web/mui/icons"

type PDFViewerHeaderDownloadButtonProps = {
  isPrivate: boolean
  documentId: string
  name: string
  color?: string
}

function PDFViewerHeaderDownloadButton({
  isPrivate,
  documentId,
  name,
  color,
}: PDFViewerHeaderDownloadButtonProps) {
  if (!isPrivate) return null

  return (
    <RPTooltip title="Download" arrow placement="bottom">
      <IconButton
        data-testid="download-button"
        onClick={() => downloadFile({ id: documentId, name }, null)}
      >
        <Download sx={{ color }} />
      </IconButton>
    </RPTooltip>
  )
}

export { PDFViewerHeaderDownloadButton }
